body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.body-container {
  min-width: -webkit-fit-content;
  min-width: -moz-fit-content;
  min-width: fit-content;
  position: relative;
}

.navbar {
  background-color: rgb(68, 67, 67);
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  flex-wrap: wrap;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
}

@-webkit-keyframes fadeIn {
  
}

@keyframes fadeIn {
  
}

.link {
  padding: 1rem;
}

.container {
  background-image: linear-gradient(
      to right bottom,
      rgba(66, 198, 238, 0.493),
      rgba(0, 199, 116, 0.432)
    ),
    url(/static/media/hero.15e5f60e.jpg);
  background-size: cover;
  height: 100%;
  width: 100%;
  font-family: 'Kanit', sans-serif;
  padding-top: 6rem;
  padding-bottom: 20rem;
}

a:link,
a:visited {
  text-decoration: none;
  color: white;
}

.header {
  font-size: 4.5rem;
  font-weight: 600;
  text-align: center;
  color: rgb(255, 170, 0);
  text-shadow: -5px 3px black;
  -webkit-animation-name: drop;
          animation-name: drop;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
}

@-webkit-keyframes drop {
  0% {
    -webkit-transform: translateY(-30rem);
            transform: translateY(-30rem);
    opacity: 0;
  }

  20% {
    -webkit-transform: translateY(-30rem);
            transform: translateY(-30rem);
    opacity: .5;
  }

  70% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }

  80% {
    -webkit-transform: translateY(-.5rem);
            transform: translateY(-.5rem);
  }

  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes drop {
  0% {
    -webkit-transform: translateY(-30rem);
            transform: translateY(-30rem);
    opacity: 0;
  }

  20% {
    -webkit-transform: translateY(-30rem);
            transform: translateY(-30rem);
    opacity: .5;
  }

  70% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }

  80% {
    -webkit-transform: translateY(-.5rem);
            transform: translateY(-.5rem);
  }

  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

.contact-item {
  margin: 0 2rem;
}

.project-grid {
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(auto-fill, 25rem);
}

.project-wrapper {
  position: relative;
  margin: 1.5rem;
  height: 31rem;
  transition: all .5s;
}

.project {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1.5rem;
  height: 100%;
  border: 3px solid black;
  box-sizing: border-box;
  background-color: rgb(60, 90, 104);
  box-shadow: 0 1rem 2.5rem rgba(0, 0, 0, 0.616);
  -webkit-animation-name: flip;
          animation-name: flip;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  transition: all .5s;
}

.project-wrapper:hover {
  -webkit-transform: scale(1.4);
          transform: scale(1.4);
  z-index: 50;
}

@media (max-width: 650px) {
  .project-wrapper:hover {
    -webkit-transform: none;
            transform: none
  }
}

.project-back {
  position: absolute;
  height: 31rem;
  width: 22rem;
  background-image: url(/static/media/card.c355e4d8.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  box-shadow: 0 1rem 2.5rem rgba(0, 0, 0, 0.616);
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  -webkit-animation-name: flipback;
          animation-name: flipback;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
}

@-webkit-keyframes flip {
  0% {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg)
  }

  100% {
    -webkit-transform: rotateY(0);
            transform: rotateY(0);
  }
}

@keyframes flip {
  0% {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg)
  }

  100% {
    -webkit-transform: rotateY(0);
            transform: rotateY(0);
  }
}

@-webkit-keyframes flipback {
  0% {
    -webkit-transform: rotateY(0);
            transform: rotateY(0)
  }

  100% {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
  }
}

@keyframes flipback {
  0% {
    -webkit-transform: rotateY(0);
            transform: rotateY(0)
  }

  100% {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
  }
}

.title {
  font-size: 2rem;
}

.pic {
  max-width: 13rem;
  max-height: 13rem;
  margin: 1rem;
}

/* // RESUME // */

.resume-background {
  width: auto;
  background-image: linear-gradient(
      to right bottom,
      rgba(66, 198, 238, 0.493),
      rgba(0, 199, 116, 0.432)
    ),
    url(/static/media/hero.15e5f60e.jpg);
  background-size: cover;
  padding: 2rem;
}

.resume {
  max-width: 40rem;
  min-height: 53rem;
  background-color: white;
  color: rgb(0, 0, 0);
  margin: auto;
  font-family: 'Source Sans Pro', sans-serif;
  padding: 1rem 1.5rem;
  box-sizing: border-box;
}

.resume a:link,
.resume a:visited {
  color: rgb(77, 77, 77);
}

.resume-bar {
  width: 95%;
  height: 0.6rem;
  background-color: rgb(24, 165, 24);
  margin: 0.5rem auto;
}

h1 {
  font-weight: 600;
  font-size: 2rem;
  margin: 0;
  padding: 0;
  padding-top: 3px;
}

.resume-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.resume-info {
  font-size: 0.9rem;
  margin-right: 1rem;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  color: rgb(77, 77, 77);
}

.resume-portfolio {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.resume-portfolio a {
  margin-top: 2px;
}

.resume-section-header {
  color: rgb(24, 165, 24);
  text-transform: uppercase;
  font-size: 1.1rem;
  font-weight: 600;
  margin: 0.7rem .5rem 0.5rem 0;
}

ul {
  margin: 0;
}

li {
  font-size: 0.9rem;
  line-height: 150%;
}

.resume-project-title {
  font-weight: 600;
  font-size: .9rem;
  margin: 0.5rem auto;
  letter-spacing: .5px;
}

.resume-position {
  font-size: .9rem;
  font-weight: 400;
}

.resume-date {
  text-transform: uppercase;
  font-weight: 600;
}

